import { productConstants } from "../actions/constants";

const initState = {
    products: [],
    totalProduct: [],
    totalCategory: [],
    priceRange: {},
    productsByPrice: {},
    pageRequest: false,
    page: {},
    error: null,
    productDetails: {},
    loading: false,
};

// eslint-disable-next-line import/no-anonymous-default-export
export default (state = initState, action) => {
    switch (action.type) {
        case productConstants.GET_PRODUCTS_BY_SLUG:
            state = {
                ...state,
                products: action.payload.products,
                priceRange: action.payload.priceRange,
                productsByPrice: {
                    ...action.payload.productsByPrice,
                },
            };
            break;

            
        case productConstants.GET_TOTAL_PRODUCTS_BY_SLUG_REQUEST:
            state = {
                ...state,
                pageRequest: true,
            };
            break;
        case productConstants.GET_TOTAL_PRODUCTS_BY_SLUG_SUCCESS:
            state = {
                ...state,
                totalProduct: action.payload.productsEachCategory,
                totalCategory: action.payload.categorys,
                pageRequest: false,
            };
            break;
        case productConstants.GET_TOTAL_PRODUCTS_BY_SLUG_FAILURE:
            state = {
                ...state,
                pageRequest: false,
                error: action.payload.error,
            };
            break;


        case productConstants.GET_PRODUCT_DETAILS_BY_ID_REQUEST:
            state = {
                ...state,
                loading: true,
            };
            break;
        case productConstants.GET_PRODUCT_DETAILS_BY_ID_SUCCESS:
            state = {
                ...state,
                loading: false,
                productDetails: action.payload.productDetails,
            };
            break;
        case productConstants.GET_PRODUCT_DETAILS_BY_ID_FAILURE:
            state = {
                ...state,
                loading: false,
                error: action.payload.error,
            };
            break;
        default:
            break;
    }

    return state;
};
